body {
  --active-link-color: #00acdc;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-weight: 500px;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

a.is-active {
  color: var(--active-link-color);
  margin: 0;
  position: relative;
}

:hover {
  color: var(--active-link-color);
}

a.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
